.design-card {
    position: relative;
    padding: 35px 0 0 35px;
    height: 100%;
    font-family: $dm-sans;
    font-weight: 700;
    background-color: $grey-50;

    @include r($xl) {
        padding: 15px 0 0 15px;
    }

    &__card-title {
        @include line-clamp(2);

        font-size: 20px;
        color: $grey-600;

        @extend %hover-link;
    }

    &__card-link {
        display: block;
        margin-bottom: 15px;
        max-width: 171px;
    }

    &__text {
        @include line-clamp(1);

        max-width: 170px;
        font-size: 16px;
        color: $main;
    }

    &__img-wrap {
        position: absolute;
        right: 0;
        bottom: 35px;
        max-width: 270px;

        @include r($xl) {
            bottom: 15px;
        }

        @include r($lg) {
            max-width: 250px;
        }

        &--sm {
            right: 35px;
            max-width: 145px;

            @include r($md) {
                right: 15px;
                max-width: 100px !important;
            }
        }
    }

    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}